$base: #251c1d;
$blue: #00abb1;

body {
	font-family: $r;
	position: relative;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  position: relative;
}

%clearfix {
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
	        box-sizing: border-box;
	&:before, &:after {
	    content: "";
	    display: table;
	}
	&:after {
	    clear: both;
	}
}

.container {
	@extend %clearfix;
}
@media (min-width: 768px) {
  .container {
    width: 766px;
  }
}
@media (min-width: 992px) {
  .container {
    width: 990px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1190px;
  }
}
a {
	text-decoration: none;
	&:hover, &:focus, &:active{
		outline: none;		
	}
}

ul {
	list-style-type: none;
}

.block_title {
	color: #fff;
    font-size: 3.5em;
	text-align: center;
	margin-top: 15px;
}

.p0  {
	padding: 0;
}

.align-left {
	text-align: left;
}
.align-right {
	text-align: right;
}
