.home {
	&-banner {
		background-image: url('../img/first-bg.png');
		background-repeat: no-repeat;
		background-position: 50% 100%;
		-webkit-background-size: cover;
		     -o-background-size: cover;
		        background-size: cover;
		float: left;
		width: 100%;
		height: 100vh;
		padding-bottom: 60vh;
		position: relative;
		z-index: 2;
		&_title {
			display: block;
			position: relative;
			margin: 150px auto 150px;
			width: 100%;
			height: 270px;
			@include respondTo(largeScreen) {
				margin: 350px auto 100px;
			}
			h1 {
				position: absolute;
				top: 25%;
				left: 25%;
				margin: 0;
				z-index: 9999!important;
				font-family: $bl;
				text: #f6fdfd center 100px;
				@include text-shadow(5px 9px 7px rgba(0,0,0,.35));
				@include transition(.2s);
				-webkit-user-select: none;
				   -moz-user-select: none;
				    -ms-user-select: none;
				        user-select: none;
				@include respondTo(tablet) {
					font-size: 85px;
					left: 18%;
				}
 			}
		}
		&_interface {
			position: absolute;
			width: 90%;
			left: 5%;
			top: -50%;
			@include transition(.4s);
			z-index: 2;
		}
		&_rectangle {
			display: block;
			position: relative;
			margin: 0 auto;
			width: 80%;
			height: 270px;
			-webkit-box-sizing: border-box;
			   -moz-box-sizing: border-box;
			        box-sizing: border-box;
			border: 20px solid #fff;
			@include box-shadow(5px 9px 7px rgba(0,0,0,.35));
			@include transition(.2s);
			z-index: 1;
			&:after {
				content: "";
				position: absolute;
				z-index: 50;
				width: 100%;
				height: 20px;
				background-color: #FFF;
				bottom: -20px;
				left: 0;
			}
		}
		&_btn {
			color: #fff;
			cursor: pointer;
			display: block;
			font-size:12px;
			text-align: center;
			font-weight: 400;
			line-height: 40px;
			float: right;
			margin-right: 10%;
			max-width: 250px;
			padding: 0 50px 0;
			position: relative;
			text-decoration: none;
			font-family: $m;
			text-transform: uppercase;
			vertical-align: middle;
			width: 100%;
			z-index: 99;
			svg {
				height: 45px;
				left: 0;
				position: absolute;
				top: 0;
				width: 100%;
			}

			rect {
				fill: none;
				stroke: #fff;
				stroke-width: 5;
				stroke-dasharray: 650, 50;
			}
			&:hover {
				font-family: $b;
				letter-spacing: 1px;
				text-decoration: none;
				color: #FFF;
				rect {
					stroke-width: 5;
					stroke-dasharray: 50, 350;
					stroke-dashoffset: 48;
					transition: all 1.35s cubic-bezier(0.19, 1, 0.2, 1);
				}
			}
		}
		&_desc {
			font-family: $b;
			color: #676363;
			font-size: 14px;
			letter-spacing: 4px;
			word-spacing: 20px;
			position: absolute;
			right: -75px;
			top: 45px;
			margin: 0;
			-webkit-transform: rotate(90deg);
			   -moz-transform: rotate(90deg);
			    -ms-transform: rotate(90deg);
			     -o-transform: rotate(90deg);
			        transform: rotate(90deg);
		}
	}
	&-partners {
		position: relative;
		float: left;
		width: 100%;
		margin-top: -60vh;
		/* background-image: url('../img/secondt-bg.png'); */
		background-repeat: no-repeat;
		background-size: 100%;
		background-position: 50% -30%;
		/* -ms-overflow-x: hidden;
		    overflow-x: hidden; */
		z-index: 2;
		.fp-tableCell {
			display: block;
			float: left;
			width: 100%;
		}
		h2 {
			text: #251c1d uppercase 40px;
			letter-spacing: 5px;
			word-spacing: 10px;
			text-align: right;
			margin-top: 125px;
    	margin-bottom: 15px;
    	@include respondTo(largeScreen) {
					margin-top: 200px;
    			margin-bottom: 50px;
			}
		}
		h3{
			float: left;
			width: 100%;
			font-family: $b;
			text: $base uppercase 30px;
			margin-bottom: 15px;
		}
		p{
			float: left;
			text: $base 18px;
			width: 100%;
			margin: 5px 0;
		}
		&__shares {
			float: right;
			width: 50%;
			-webkit-box-sizing: border-box;
			   -moz-box-sizing: border-box;
			        box-sizing: border-box;
			padding-left: 25px;
			&_icon {
				float: left;
				width: 30%;
			}
			&_desc {
				width: 70%;
				float: left;
				h3 {
					text: #251c1d 25px;
					margin-top: 0;
					strong {
						display: block;
						text: $blue 32px;
					}
				}
				&_btn {
					@extend %blue_btn;
					font-size: 25px;
					width: 100%;
				}
			}
		}
		&_mockup {
			position: absolute;
			left: 5%;
			top: 120px;
			z-index: 10;
			width: 45%;
		}
	}
	&-services {
		float: left;
		width: 100%;
		position: relative;
		padding: 50px 0;
		color: #251C1D;
		canvas {
			display: block;
			vertical-align: bottom;
		}
		h3 {
			font-family: $l;
			font-size: 35px;
			letter-spacing: 5px;
			margin: 10px 0;
			strong {
				font-family: $b;
			}
		}
		&__flex {
			display: flex;
			-webkit-flex-wrap: wrap;
			    -ms-flex-wrap: wrap;
			        flex-wrap: wrap;
			-webkit-align-items: flex-start;
			        align-items: flex-start;
			-webkit-justify-content: center;
			        justify-content: center;
		}
		&_item {
			width: 250px;
			margin: 15px 50px;
			@include transition(.4s);
			&:hover, &:focus {
				.home__services_icon {
					transform: scale(1.2);
				}
			}
		}
		&_icon {
			width: 100%;
			height: 150px;
			position: relative;
			text-align: center;
			margin-bottom: 15px;
		}
		&_title {
			float: left;
			width: 100%;
			pointer-events: initial;
			h4 {
				float: left;
				position: relative;
				padding-bottom: 25px;
				padding-right: 20px;
				font-family: $b;
				text: #251c1d uppercase 25px;
				margin: 10px 0;
				&:before, &:after {
					content: "";
					position: absolute;
				}
				&:before {
					width: 58px;
					height: 6px;
					background-color: #251b1d;
					left: 20px;
					bottom: 15px;
				}
				&:after {
					bottom: 0;
					right: 0;
					width: 4px;
					height: 42px;
					background-color: #00a1a7;
				}
			}
		}
		&_price {
			margin-top: 15px;
			margin-bottom: 5px;
			text: $base right 23px;
			strong {
				font-family: $b;
				text: $blue 28px;
				@include transition(.3s);
			}
		}
		&_list {
			float: left;
			width: 100%;
			padding: 0;
			li {					
				margin-bottom: 15px;
				pointer-events: initial;
				a {
					text: #251c1d uppercase 16px;
				}
			}
		}
	}

	&-steps {
		float: left;
		width: 100%;
		position: relative;
		padding: 50px 0;
		z-index: 9999;
		&__title {
			float: left;
			width: 100%;
			h2 {
				float: left;
				width: 45%;
				text: $base uppercase 40px 1;
				margin: 25px 0;
				strong {
					float: left;
					font-family: $b;
					margin-right: 10px;
					text: $blue 100px 0.85;
				}
				span {
					color: $blue;
				}
			}
		}
		&__container {
			width: 100%;
			/* height: 70vh; */
		}
		&__item {
			padding-bottom: 50px;
			&_thumb {
				float: left;
				width: 50%;
				height: 400px;
				@include overflow-img;
				img {
					max-width: 125%;
				}
			}
			.flexw {
				display: flex;
				height: 100%;
				display: flex;
		       	-webkit-align-items: stretch;
		       	        align-items: stretch;
				-webkit-flex-wrap: wrap;
				    -ms-flex-wrap: wrap;
				        flex-wrap: wrap;
				-webkit-flex-direction: row;
				    -ms-flex-direction: row;
				        flex-direction: row;
			}
			&_description {
				float: left;

				height: 400px;

				width: 50%;
				box-sizing: border-box;
				padding: 0 20px;
				&_title {
					-webkit-flex-basis: 100%;
					        flex-basis: 100%;
					width: 100%;
					float: left;
					text: $base uppercase 30px;
					margin: 0;
					padding-right: 80px;
					border-bottom: 1px solid #E6E8E7;
					span {
						float: right;
						position: absolute;
						top: 0;
						right: 0;
						font-family: $ul;
						font-weight: 100;
						text: #bab9b9 75px 1;
					}
					strong {
						font-family: $b;
					}
				}
				&_text {
					-webkit-flex-basis: 100%;
					        flex-basis: 100%;
					float: left;
					width: 100%;
					margin: 15px 0;
					p {
						padding: 0;
						text: $base 21px 1;
					}
				}
				&_btn {
					@extend .home-banner_btn;
					width: 75%;
					max-width: 100%;
					display: inline-block;
					text: #f5f0ec 28px;
					padding: 0 20px;
					background-color: $blue;
					border: 1px solid;
					overflow: hidden;
					position: relative;
					height: 60px;
					line-height: 50px;
					margin: 0;
					float: none;
					@include transition(.2s);
					-webkit-align-self: flex-end;
					        align-self: flex-end;
					span {
						z-index: 20;
					}
					&:after {
						background: #fff;
						content: "";
						height: 155px;
						left: -75px;
						opacity: .2;
						position: absolute;
						top: -50px;
						transform: rotate(35deg);
						transition: all .8s cubic-bezier(0.19, 1, 0.22, 1);
						width: 50px;
						z-index: -10;
					}
					&:hover {
						font-family: $m;
						&:after {
							left: 120%;
							transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
						}
					}
			 	}
			}
		}
	}

	&-projects {
		float: left;
		width: 100%;
		position: relative;
		h2 {
			text: $base uppercase 38px 1;
			margin-bottom: 10px;
			margin-top: 0;
			@include respondTo(largeScreen){
		     	font-size: 54px;
		     	margin-bottom: 15px;
		    }
		}
		h4 {
			text: $base uppercase 14px 1;
			margin: 5px 0;
			@include respondTo(largeScreen){
				margin: 15px 0;
			}

		}
		&:before {
			content: "";
			position: absolute;
			width: 100%;
			height: 50vh;
			top: 50%;
			transform: translateY(-50%);
			background-color: $base;
		}
		&__flex {
			display: flex;
			margin: 25px 0;
			-webkit-justify-content: space-between;
			        justify-content: space-between;
			 -webkit-flex-wrap: wrap;
			     -ms-flex-wrap: wrap;
			         flex-wrap: wrap;
			-webkit-flex-direction: row;
			    -ms-flex-direction: row;
			        flex-direction: row;
		    width: 100%;
		}
		&__more {
			position: relative;
			-webkit-flex-basis: 49.5%;
			        flex-basis: 49.5%;
		    height: 280px;
		    @include overflow-img;
		    margin: 5px 0;
		    @include transition(.3s);
		    @include respondTo(largeScreen){
		     	height: 320px;
		    }
		    &_line {
		    	float: left;
		    	width: 100%;
		    	display: flex;
		    	-webkit-flex-wrap: wrap;
		    	    -ms-flex-wrap: wrap;
		    	        flex-wrap: wrap;
		    	    -webkit-flex-direction: row;
		    	        -ms-flex-direction: row;
		    	            flex-direction: row;
		    }
		    &_diamond {
		    	size: 60px;
		    	position: absolute;
		    	left: 50%;
		    	top: 260px;
		    	background-color: #FFF;
		    	-webkit-transform: rotate(45deg) translateX(-70%);
		    	   -moz-transform: rotate(45deg) translateX(-70%);
		    	    -ms-transform: rotate(45deg) translateX(-70%);
		    	     -o-transform: rotate(45deg) translateX(-70%);
		    	        transform: rotate(45deg) translateX(-70%);
		    	z-index: 100;
				@include respondTo(largeScreen){
					top: 280px;
				}
		    	img {
		    		position: absolute;
		    		top: 50%;
		    		left: 50%;
		    		z-index: 9999;
		    		-webkit-transform: translate(-50%, -50%) rotate(-45deg);
		    		   -moz-transform: translate(-50%, -50%) rotate(-45deg);
		    		    -ms-transform: translate(-50%, -50%) rotate(-45deg);
		    		     -o-transform: translate(-50%, -50%) rotate(-45deg);
		    		        transform: translate(-50%, -50%) rotate(-45deg);
		    	}
		    }
		    &_hover {
		    	opacity: 0;
		    	position: absolute;
		    	top: 0;
		    	right: 0;
		    	bottom: 0;
		    	left: 0;
				background-color: rgba(248, 242, 242, .5);
				@include transition(.3s);
				color: $base;
				&_box {
					display: block;
					position: absolute;
					height: 220px;
					top: 50%;
					left: 50%;
					-webkit-transform: translate(-50%, -50%);
					   -moz-transform: translate(-50%, -50%);
					    -ms-transform: translate(-50%, -50%);
					     -o-transform: translate(-50%, -50%);
					        transform: translate(-50%, -50%);
					width: 75%;
					border: 2px solid $base;
					margin: auto;
					text-align: center;
				}
		    }
		    &_type {
		    	display: block;
		    	font-family: $b;
		    	text-shadow: 0px 0px 5px $blue ;
		    	top: 50%;
				left: 50%;
				width: 100%;
				-webkit-transform: translate(-50%, -50%);
				   -moz-transform: translate(-50%, -50%);
				    -ms-transform: translate(-50%, -50%);
				     -o-transform: translate(-50%, -50%);
				        transform: translate(-50%, -50%);
		    	position: absolute;
		    	text: uppercase 38px;
		    }
		    &_btn {
		    	padding: 10px 15px;
		    	text: $base uppercase 14px;
		    	background-color: $blue;
		    	position: absolute;
		    	border: 2px solid $base;
		    	bottom: 0;
		    	left: 50%;
		    	-webkit-transform: translate(-50%, 50%);
		    	   -moz-transform: translate(-50%, 50%);
		    	    -ms-transform: translate(-50%, 50%);
		    	     -o-transform: translate(-50%, 50%);
		    	        transform: translate(-50%, 50%);
		    	overflow: hidden;
		    	span {
					z-index: 20;
				}
				&:after {
					background: #fff;
					content: "";
					height: 155px;
					left: -75px;
					opacity: .2;
					position: absolute;
					top: -50px;
					transform: rotate(35deg);
					transition: all .8s cubic-bezier(0.19, 1, 0.22, 1);
					width: 50px;
					z-index: -10;
				}
				&:hover {
					font-family: $m;
					&:after {
						left: 120%;
						transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
					}
				}
		    }
		    &:hover {
		    	.home-projects__more_hover {
		    		opacity: 1;
		    		@include transition(.3s);
		    	}
	         	img {
	         		-webkit-transform: translate(-50%,-50%) scale(1.1);
	         		   -moz-transform: translate(-50%,-50%) scale(1.1);
	         		    -ms-transform: translate(-50%,-50%) scale(1.1);
	         		     -o-transform: translate(-50%,-50%) scale(1.1);
         		        transform: translate(-50%,-50%) scale(1.1);
	         	}
	         }
		}
		&__tabs {
			-webkit-flex-basis: 100%;
			        flex-basis: 100%;
		    display: flex;
		    -webkit-justify-content: space-between;
		            justify-content: space-between;
		    width: 100%;

		    margin: 5px 0;
		    &_content {
	         	display: none;	         	
	         }
		    &_item {
		    	-webkit-flex-basis: 32.8%;
		    	        flex-basis: 32.8%;
		         @include overflow-img;
		         height: 180px;		         
		         background-color: $base;
		         img {
		         	@include transition(.3s);

		         }
		         &:hover {
		         	img {
		         		-webkit-transform: translate(-50%,-50%) scale(1.1);
		         		   -moz-transform: translate(-50%,-50%) scale(1.1);
		         		    -ms-transform: translate(-50%,-50%) scale(1.1);
		         		     -o-transform: translate(-50%,-50%) scale(1.1);
		         		        transform: translate(-50%,-50%) scale(1.1);
		         	}
		         }
		         @include respondTo(largeScreen){
			     	height: 225px;
			    }
		    }
		}
		&__nav {
			-webkit-flex-basis: 100%;
			        flex-basis: 100%;
		    width: 100%;
		    clip-path: polygon(0 0, 0% 60%, 4% 100%, 100% 100%, 100% 0%,50% 0);
		    ul {
		    	padding: 0;
		    	text-align: center;
		    	margin: 0;
		    	float: left;
		    	width: 100%;
		    	li {
		    		float: left;
		    		width: 1 / 3 * 100%;
		    		a {
		    			position: relative;
		    		}
		    	}
		    }
		    &_btn {
		    	float: left;
    			width: 100%;
    			padding: 25px 0;
    			background-color: #080001;
    			text: #fff uppercase 22px;
    			@include transition(.3s);
    			 overflow: hidden;
		    	span {
					z-index: 20;
				}
				&:hover {
					font-family: $m;
					background-color: lighten(#080001,10%);
				}
				&_active {
					background-color: $blue;
					&:hover {
						font-family: $r;
						background-color: $blue;
					}
				}
		    }
		}
	}
	&-order {
		float: left;
		width: 100%;
		position: relative;
		background-image: url('../img/551dbabed3a43.jpg');
		background-repeat: no-repeat;
		-webkit-background-size: cover;
		     -o-background-size: cover;
		        background-size: cover;
		background-position: 50% 50%;
		&__box {
			float: left;
			width: 40%;
			margin-bottom: 150px;
			h2 {
				font-family: $bl;
				text: $base uppercase 53px 1;
				text-shadow: 0px 0px 3px #fff;
				span {
					color: $blue;
				}
			}
		}
		&_btn {
			@extend .home-steps__item_description_btn;
		}
		small {
			float: left;
			margin-top: 25px;
			font-family: $l;
			text: $base 10px;
		}
	}
}

.show {
	&_smm, &_design {
		display: none;
	}
}
